*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root {
  --dark-background: #112233;
  --dark-nav: #223344;
}
.color{
  background-color: blanchedalmond;
  width: 10px;
  height: 10px;
}
body {
  width: 100%;
  height: 100vh;
  background-color: var(--dark-background);
  display: flex;
  justify-content: center;
  align-items: center;
}
.app, .ending-card{
  background-color: var(--dark-nav);
  width: 500px;
  height: 200px;
  border-radius: 1em;
}
.ending-text {
  width: 100%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: springgreen;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
}
.dots-conteiner{
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dot{
  width: 12px;
  height: 12px;
  border-radius: 100%;
  border: 2px solid springgreen;
  margin: 0 10px;
}
.active-dot {
  background-color: springgreen;
}
.question-conteiner {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.question{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
  color: springgreen;
  padding: 0 10px;
  font-size: 26px;
}
.answers-conteiner{
  width: 100%;
  height: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-evenly;
}
.answer-wraper{
  background-color: springgreen;
  width: 45%;
  height: 40%;
  border-radius: 1em;
  transition: 0.2s;
}
.answer-wraper:hover{
  background-color: rgba(0, 255, 128, 0.856);
}
.answer{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--dark-nav);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
  border-radius: 1em;
  cursor: pointer;
  user-select: none;
}
.play-again-conteiner {
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.play-again-button {
  background-color: springgreen;
  width: 45%;
  height: 40%;
  border-radius: 1em;
  transition: 0.2s;
}
.play-again-button:hover{
  background-color: rgba(0, 255, 128, 0.856);
}
.play-again {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--dark-nav);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
  border-radius: 1em;
  cursor: pointer;
  user-select: none;  
}
@media only screen and (max-width: 560px) {
  body {
    width: auto;
    height: auto;
    display: block;
  }
  .app, .ending-card{
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }
  .answers-conteiner {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }
  .answer-wraper{
    width: 95%;
    height: 22%;
    font-size: 25px;
    -webkit-tap-highlight-color: transparent;
  }
  .answer {
    padding: 10px;
  }
  .ending-text{
    align-items: flex-end;
    padding-bottom: 20px;
  }
  .play-again-conteiner{
    align-items: flex-start;
  }
  .play-again-button{
    width: 95%;
    height: 25%;
    -webkit-tap-highlight-color: transparent;
  }
  .play-again{
    font-size: 30px;
    -webkit-tap-highlight-color: transparent;
  }
}